"use client";

import { useState, useEffect, useRef } from "react";
import { Card, CardContent } from "@/components/ui/card";

export default function TestimonialsSection() {
  const [isVisible, setIsVisible] = useState(false);
  const [currentTestimonial, setCurrentTestimonial] = useState(0);
  const sectionRef = useRef<HTMLElement>(null);
  const intervalRef = useRef<NodeJS.Timeout | null>(null);

  const testimonials = [
    {
      name: "Sarah Chen",
      position: "CEO",
      company: "TechFlow Solutions",
      content: "Viktor's AI-powered sales strategy transformed our entire business model. We saw a 340% increase in qualified leads within just 3 months. His approach to combining technology with human psychology is truly revolutionary.",
      rating: 5,
      image: "https://placehold.co/80x80?text=Sarah+Chen+professional+headshot"
    },
    {
      name: "Michael Rodriguez",
      position: "VP of Marketing",
      company: "GrowthCorp",
      content: "Working with Viktor was a game-changer for our marketing efforts. His data-driven approach and deep understanding of consumer behavior helped us achieve our highest ROI ever - 285% increase in just 6 months.",
      rating: 5,
      image: "https://placehold.co/80x80?text=Michael+Rodriguez+professional+headshot"
    },
    {
      name: "Emily Watson",
      position: "Founder",
      company: "InnovateLab",
      content: "Viktor's growth strategies are simply unmatched. He doesn't just provide solutions; he transforms the way you think about business growth. Our revenue doubled in the first quarter after implementing his recommendations.",
      rating: 5,
      image: "https://placehold.co/80x80?text=Emily+Watson+professional+headshot"
    },
    {
      name: "David Kim",
      position: "COO",
      company: "ScaleUp Ventures",
      content: "The level of expertise and innovation Viktor brings is extraordinary. His AI automation systems streamlined our operations and increased our conversion rates by 156%. Absolutely phenomenal results.",
      rating: 5,
      image: "https://placehold.co/80x80?text=David+Kim+professional+headshot"
    },
    {
      name: "Lisa Thompson",
      position: "Director of Sales",
      company: "ProSales Inc",
      content: "Viktor's approach to sales psychology and automation is brilliant. We went from struggling with lead qualification to having a fully optimized pipeline that converts at 67% higher rates. Incredible transformation.",
      rating: 5,
      image: "https://placehold.co/80x80?text=Lisa+Thompson+professional+headshot"
    }
  ];

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true);
          startAutoSlide();
        } else {
          stopAutoSlide();
        }
      },
      { threshold: 0.2 }
    );

    if (sectionRef.current) {
      observer.observe(sectionRef.current);
    }

    return () => {
      observer.disconnect();
      stopAutoSlide();
    };
  }, []);

  const startAutoSlide = () => {
    stopAutoSlide();
    intervalRef.current = setInterval(() => {
      setCurrentTestimonial((prev) => (prev + 1) % testimonials.length);
    }, 5000);
  };

  const stopAutoSlide = () => {
    if (intervalRef.current) {
      clearInterval(intervalRef.current);
      intervalRef.current = null;
    }
  };

  const goToTestimonial = (index: number) => {
    setCurrentTestimonial(index);
    startAutoSlide();
  };

  const nextTestimonial = () => {
    setCurrentTestimonial((prev) => (prev + 1) % testimonials.length);
    startAutoSlide();
  };

  const prevTestimonial = () => {
    setCurrentTestimonial((prev) => (prev - 1 + testimonials.length) % testimonials.length);
    startAutoSlide();
  };

  const handleImageError = (e: React.SyntheticEvent<HTMLImageElement, Event>) => {
    const target = e.target as HTMLImageElement;
    target.style.display = 'none';
  };

  const renderStars = (rating: number) => {
    return Array.from({ length: 5 }, (_, i) => (
      <span
        key={i}
        className={`text-xl ${i < rating ? 'text-yellow-400' : 'text-gray-300 dark:text-gray-600'}`}
      >
        ★
      </span>
    ));
  };

  return (
    <section 
      id="testimonials" 
      ref={sectionRef}
      className="py-20 px-4 bg-white dark:bg-gray-900"
    >
      <div className="max-w-6xl mx-auto">
        <div className={`text-center mb-16 ${isVisible ? 'slide-up' : 'scroll-reveal'}`}>
          <h2 className="section-title font-bold text-gray-900 dark:text-white mb-6">
            Client Success Stories
          </h2>
          <p className="text-xl text-gray-600 dark:text-gray-300 max-w-3xl mx-auto">
            Hear from business leaders who have transformed their companies with our proven strategies
          </p>
        </div>

        {/* Main Testimonial Display */}
        <div className={`relative ${isVisible ? 'scale-in' : 'scroll-reveal'}`} style={{ animationDelay: '0.2s' }}>
          <Card className="glass-card border-0 overflow-hidden">
            <CardContent className="p-8 md:p-12">
              <div className="text-center">
                {/* Stars */}
                <div className="flex justify-center mb-6">
                  {renderStars(testimonials[currentTestimonial].rating)}
                </div>

                {/* Quote */}
                <blockquote className="text-xl md:text-2xl text-gray-700 dark:text-gray-300 leading-relaxed mb-8 italic">
                  "{testimonials[currentTestimonial].content}"
                </blockquote>

                {/* Author Info */}
                <div className="flex items-center justify-center space-x-4">
                  <img
                    src={testimonials[currentTestimonial].image}
                    alt={`${testimonials[currentTestimonial].name} professional headshot`}
                    className="w-16 h-16 rounded-full object-cover border-2 border-white/20"
                    onError={handleImageError}
                  />
                  <div className="text-left">
                    <div className="font-semibold text-gray-900 dark:text-white text-lg">
                      {testimonials[currentTestimonial].name}
                    </div>
                    <div className="text-gray-600 dark:text-gray-400">
                      {testimonials[currentTestimonial].position}
                    </div>
                    <div className="text-blue-600 dark:text-blue-400 font-medium">
                      {testimonials[currentTestimonial].company}
                    </div>
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Navigation Arrows */}
          <button
            onClick={prevTestimonial}
            className="absolute left-4 top-1/2 transform -translate-y-1/2 glass-button p-3 rounded-full hover:bg-white/20 transition-all duration-300"
            aria-label="Previous testimonial"
          >
            <svg className="w-6 h-6 text-gray-700 dark:text-gray-300" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
            </svg>
          </button>
          <button
            onClick={nextTestimonial}
            className="absolute right-4 top-1/2 transform -translate-y-1/2 glass-button p-3 rounded-full hover:bg-white/20 transition-all duration-300"
            aria-label="Next testimonial"
          >
            <svg className="w-6 h-6 text-gray-700 dark:text-gray-300" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
            </svg>
          </button>
        </div>

        {/* Dots Indicator */}
        <div className={`flex justify-center space-x-2 mt-8 ${isVisible ? 'fade-in' : 'scroll-reveal'}`} style={{ animationDelay: '0.4s' }}>
          {testimonials.map((_, index) => (
            <button
              key={index}
              onClick={() => goToTestimonial(index)}
              className={`w-3 h-3 rounded-full transition-all duration-300 ${
                index === currentTestimonial
                  ? 'bg-blue-600 dark:bg-blue-400 scale-125'
                  : 'bg-gray-300 dark:bg-gray-600 hover:bg-gray-400 dark:hover:bg-gray-500'
              }`}
              aria-label={`Go to testimonial ${index + 1}`}
            />
          ))}
        </div>

        {/* Testimonial Grid Preview */}
        <div className={`mt-16 grid md:grid-cols-3 gap-6 ${isVisible ? 'slide-up' : 'scroll-reveal'}`} style={{ animationDelay: '0.6s' }}>
          {testimonials.slice(0, 3).map((testimonial, index) => (
            <div
              key={index}
              className="glass p-6 cursor-pointer smooth-transition hover:scale-105"
              onClick={() => goToTestimonial(index)}
            >
              <div className="flex justify-center mb-3">
                {renderStars(testimonial.rating)}
              </div>
              <p className="text-gray-700 dark:text-gray-300 text-sm mb-4 line-clamp-3">
                "{testimonial.content.substring(0, 120)}..."
              </p>
              <div className="text-center">
                <div className="font-semibold text-gray-900 dark:text-white text-sm">
                  {testimonial.name}
                </div>
                <div className="text-gray-600 dark:text-gray-400 text-xs">
                  {testimonial.company}
                </div>
              </div>
            </div>
          ))}
        </div>

        {/* Trust Indicators */}
        <div className={`mt-16 text-center ${isVisible ? 'fade-in' : 'scroll-reveal'}`} style={{ animationDelay: '0.8s' }}>
          <div className="glass-card p-8">
            <h3 className="text-xl font-bold text-gray-900 dark:text-white mb-6">
              Trusted by Industry Leaders
            </h3>
            <div className="grid grid-cols-2 md:grid-cols-4 gap-8 items-center">
              <div className="text-center">
                <div className="text-2xl font-bold text-blue-600 dark:text-blue-400">500+</div>
                <div className="text-gray-600 dark:text-gray-400 text-sm">Happy Clients</div>
              </div>
              <div className="text-center">
                <div className="text-2xl font-bold text-green-600 dark:text-green-400">98%</div>
                <div className="text-gray-600 dark:text-gray-400 text-sm">Success Rate</div>
              </div>
              <div className="text-center">
                <div className="text-2xl font-bold text-purple-600 dark:text-purple-400">4.9/5</div>
                <div className="text-gray-600 dark:text-gray-400 text-sm">Average Rating</div>
              </div>
              <div className="text-center">
                <div className="text-2xl font-bold text-orange-600 dark:text-orange-400">24/7</div>
                <div className="text-gray-600 dark:text-gray-400 text-sm">Support</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}
