"use client";

import { useState, useEffect, useRef } from "react";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";

export default function SubscriptionSection() {
  const [isVisible, setIsVisible] = useState(false);
  const [email, setEmail] = useState('');
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitStatus, setSubmitStatus] = useState<'idle' | 'success' | 'error'>('idle');
  const [emailError, setEmailError] = useState('');
  const sectionRef = useRef<HTMLElement>(null);

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true);
        }
      },
      { threshold: 0.3 }
    );

    if (sectionRef.current) {
      observer.observe(sectionRef.current);
    }

    return () => observer.disconnect();
  }, []);

  const validateEmail = (email: string): boolean => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return emailRegex.test(email);
  };

  const handleEmailChange = (value: string) => {
    setEmail(value);
    if (emailError) {
      setEmailError('');
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!email.trim()) {
      setEmailError('Email is required');
      return;
    }

    if (!validateEmail(email)) {
      setEmailError('Please enter a valid email address');
      return;
    }

    setIsSubmitting(true);
    setSubmitStatus('idle');

    try {
      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 1500));
      
      // Log subscription (in real implementation, this would be sent to your email service)
      console.log('Email subscription:', { email, timestamp: new Date().toISOString() });
      
      // Store in localStorage for demo purposes
      const subscriptions = JSON.parse(localStorage.getItem('emailSubscriptions') || '[]');
      subscriptions.push({ email, timestamp: new Date().toISOString() });
      localStorage.setItem('emailSubscriptions', JSON.stringify(subscriptions));
      
      setSubmitStatus('success');
      setEmail('');
      
      // Reset success message after 5 seconds
      setTimeout(() => setSubmitStatus('idle'), 5000);
    } catch (error) {
      console.error('Subscription error:', error);
      setSubmitStatus('error');
    } finally {
      setIsSubmitting(false);
    }
  };

  const benefits = [
    "Weekly growth strategies and insights",
    "Exclusive AI automation tips",
    "Case studies and success stories",
    "Early access to new resources",
    "Industry trend analysis",
    "Free consultation opportunities"
  ];

  return (
    <section 
      id="subscription" 
      ref={sectionRef}
      className="py-20 px-4 bg-gradient-to-br from-purple-900 via-blue-900 to-indigo-900 text-white"
    >
      <div className="max-w-6xl mx-auto">
        <div className={`text-center mb-16 ${isVisible ? 'slide-up' : 'scroll-reveal'}`}>
          <h2 className="section-title font-bold mb-6">
            Stay Ahead of the Growth Curve
          </h2>
          <p className="text-xl text-white/90 max-w-3xl mx-auto">
            Join thousands of business leaders who receive exclusive insights, strategies, and tools 
            to accelerate their growth journey.
          </p>
        </div>

        <div className="grid lg:grid-cols-2 gap-12 items-center">
          {/* Subscription Form */}
          <div className={`${isVisible ? 'scale-in' : 'scroll-reveal'}`} style={{ animationDelay: '0.2s' }}>
            <div className="glass-card p-8 bg-white/10">
              <h3 className="text-2xl font-bold mb-6">
                Get Weekly Growth Insights
              </h3>
              
              <form onSubmit={handleSubmit} className="space-y-4">
                <div>
                  <Input
                    type="email"
                    value={email}
                    onChange={(e) => handleEmailChange(e.target.value)}
                    className={`form-input text-white placeholder-white/60 ${emailError ? 'error' : ''}`}
                    placeholder="Enter your email address"
                  />
                  {emailError && (
                    <p className="text-red-400 text-sm mt-1">{emailError}</p>
                  )}
                </div>

                <Button
                  type="submit"
                  disabled={isSubmitting}
                  className="w-full glass-button py-3 px-6 text-white font-semibold text-lg hover:text-white transition-colors duration-300 disabled:opacity-50"
                >
                  {isSubmitting ? (
                    <div className="flex items-center justify-center space-x-2">
                      <div className="loading-spinner"></div>
                      <span>Subscribing...</span>
                    </div>
                  ) : (
                    'Subscribe Now - It\'s Free!'
                  )}
                </Button>

                {/* Status Messages */}
                {submitStatus === 'success' && (
                  <div className="p-4 bg-green-500/20 border border-green-400/30 rounded-lg">
                    <p className="text-green-200 font-medium">
                      🎉 Welcome aboard! Check your email for a confirmation message.
                    </p>
                  </div>
                )}

                {submitStatus === 'error' && (
                  <div className="p-4 bg-red-500/20 border border-red-400/30 rounded-lg">
                    <p className="text-red-200 font-medium">
                      Sorry, there was an error. Please try again or contact us directly.
                    </p>
                  </div>
                )}

                <p className="text-white/70 text-sm text-center">
                  No spam, ever. Unsubscribe anytime with one click.
                </p>
              </form>
            </div>
          </div>

          {/* Benefits List */}
          <div className={`${isVisible ? 'slide-up' : 'scroll-reveal'}`} style={{ animationDelay: '0.4s' }}>
            <h3 className="text-2xl font-bold mb-8">
              What You&apos;ll Get:
            </h3>
            
            <div className="space-y-4">
              {benefits.map((benefit, index) => (
                <div 
                  key={benefit}
                  className="flex items-start space-x-4 p-4 glass rounded-lg smooth-transition hover:bg-white/10"
                  style={{ animationDelay: `${0.6 + index * 0.1}s` }}
                >
                  <div className="w-6 h-6 bg-gradient-to-br from-green-400 to-blue-500 rounded-full flex items-center justify-center flex-shrink-0 mt-0.5">
                    <svg className="w-4 h-4 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
                    </svg>
                  </div>
                  <span className="text-white/90 font-medium">{benefit}</span>
                </div>
              ))}
            </div>

            {/* Social Proof */}
            <div className="mt-8 p-6 glass rounded-lg">
              <div className="flex items-center justify-between mb-4">
                <h4 className="text-lg font-semibold">Join Our Community</h4>
                <div className="flex -space-x-2">
                  {[1, 2, 3, 4, 5].map((i) => (
                    <div 
                      key={i}
                      className="w-8 h-8 bg-gradient-to-br from-blue-400 to-purple-500 rounded-full border-2 border-white/20"
                    ></div>
                  ))}
                  <div className="w-8 h-8 bg-white/20 rounded-full border-2 border-white/20 flex items-center justify-center">
                    <span className="text-xs font-bold">+</span>
                  </div>
                </div>
              </div>
              <div className="grid grid-cols-3 gap-4 text-center">
                <div>
                  <div className="text-2xl font-bold text-blue-400">15K+</div>
                  <div className="text-white/70 text-sm">Subscribers</div>
                </div>
                <div>
                  <div className="text-2xl font-bold text-green-400">4.8/5</div>
                  <div className="text-white/70 text-sm">Rating</div>
                </div>
                <div>
                  <div className="text-2xl font-bold text-purple-400">98%</div>
                  <div className="text-white/70 text-sm">Open Rate</div>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Additional CTA */}
        <div className={`mt-16 text-center ${isVisible ? 'fade-in' : 'scroll-reveal'}`} style={{ animationDelay: '0.8s' }}>
          <div className="glass-card p-8 max-w-4xl mx-auto">
            <h3 className="text-2xl font-bold mb-4">
              Ready to Transform Your Business?
            </h3>
            <p className="text-white/90 mb-6 text-lg">
              Don&apos;t wait for success to find you. Take the first step towards exponential growth today.
            </p>
            <div className="flex flex-col sm:flex-row gap-4 justify-center">
              <button
                onClick={() => {
                  const contactSection = document.getElementById('contact');
                  if (contactSection) {
                    contactSection.scrollIntoView({ behavior: 'smooth' });
                  }
                }}
                className="glass-button px-8 py-3 text-white font-semibold hover:text-white transition-colors duration-300"
              >
                Get Free Consultation
              </button>
              <button
                onClick={() => {
                  const servicesSection = document.getElementById('services');
                  if (servicesSection) {
                    servicesSection.scrollIntoView({ behavior: 'smooth' });
                  }
                }}
                className="px-8 py-3 text-white/90 font-medium border border-white/30 rounded-xl hover:bg-white/10 transition-all duration-300"
              >
                Explore Services
              </button>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}
