"use client";

import { useState, useEffect, useRef } from "react";
import { Card, CardContent } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";

export default function PortfolioSection() {
  const [isVisible, setIsVisible] = useState(false);
  const [activeCase, setActiveCase] = useState(0);
  const sectionRef = useRef<HTMLElement>(null);

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true);
        }
      },
      { threshold: 0.2 }
    );

    if (sectionRef.current) {
      observer.observe(sectionRef.current);
    }

    return () => observer.disconnect();
  }, []);

  const caseStudies = [
    {
      title: "E-Commerce Growth Transformation",
      client: "TechRetail Inc.",
      industry: "E-Commerce",
      challenge: "Declining conversion rates and high customer acquisition costs",
      solution: "Implemented AI-powered personalization engine and automated email marketing sequences",
      results: {
        revenue: "+285%",
        conversion: "+156%",
        cac: "-67%",
        timeline: "6 months"
      },
      image: "https://placehold.co/600x400?text=E-Commerce+dashboard+with+analytics+and+growth+charts",
      tags: ["AI Personalization", "Email Automation", "Conversion Optimization"]
    },
    {
      title: "SaaS Sales Pipeline Optimization",
      client: "CloudSoft Solutions",
      industry: "SaaS",
      challenge: "Long sales cycles and low lead qualification efficiency",
      solution: "Deployed predictive lead scoring and automated nurturing workflows",
      results: {
        revenue: "+340%",
        conversion: "+89%",
        cac: "-45%",
        timeline: "4 months"
      },
      image: "https://placehold.co/600x400?text=SaaS+sales+pipeline+dashboard+with+conversion+metrics",
      tags: ["Lead Scoring", "Sales Automation", "Pipeline Management"]
    },
    {
      title: "Manufacturing Digital Transformation",
      client: "Industrial Dynamics",
      industry: "Manufacturing",
      challenge: "Outdated marketing strategies and poor digital presence",
      solution: "Complete digital marketing overhaul with AI-driven content strategy",
      results: {
        revenue: "+198%",
        conversion: "+234%",
        cac: "-52%",
        timeline: "8 months"
      },
      image: "https://placehold.co/600x400?text=Manufacturing+digital+transformation+analytics+dashboard",
      tags: ["Digital Marketing", "Content Strategy", "Brand Positioning"]
    }
  ];

  const handleImageError = (e: React.SyntheticEvent<HTMLImageElement, Event>) => {
    const target = e.target as HTMLImageElement;
    target.style.display = 'none';
  };

  return (
    <section 
      id="portfolio" 
      ref={sectionRef}
      className="py-20 px-4 bg-gradient-to-br from-gray-50 to-blue-50 dark:from-gray-900 dark:to-blue-900"
    >
      <div className="max-w-7xl mx-auto">
        <div className={`text-center mb-16 ${isVisible ? 'slide-up' : 'scroll-reveal'}`}>
          <h2 className="section-title font-bold text-gray-900 dark:text-white mb-6">
            Success Stories
          </h2>
          <p className="text-xl text-gray-600 dark:text-gray-300 max-w-3xl mx-auto">
            Real results from real businesses. Discover how our strategies have transformed companies across industries.
          </p>
        </div>

        {/* Case Study Navigation */}
        <div className={`flex flex-wrap justify-center gap-4 mb-12 ${isVisible ? 'fade-in' : 'scroll-reveal'}`} style={{ animationDelay: '0.2s' }}>
          {caseStudies.map((study, index) => (
            <button
              key={index}
              onClick={() => setActiveCase(index)}
              className={`px-6 py-3 rounded-xl font-medium transition-all duration-300 ${
                activeCase === index
                  ? 'glass-button text-gray-900 dark:text-white'
                  : 'bg-white/50 dark:bg-gray-800/50 text-gray-600 dark:text-gray-400 hover:bg-white/70 dark:hover:bg-gray-700/70'
              }`}
            >
              {study.client}
            </button>
          ))}
        </div>

        {/* Active Case Study */}
        <div className={`${isVisible ? 'scale-in' : 'scroll-reveal'}`} style={{ animationDelay: '0.4s' }}>
          <Card className="glass-card border-0 overflow-hidden">
            <CardContent className="p-0">
              <div className="grid lg:grid-cols-2 gap-0">
                {/* Image Section */}
                <div className="relative h-64 lg:h-auto">
                  <img
                    src={caseStudies[activeCase].image}
                    alt={`${caseStudies[activeCase].title} dashboard and analytics`}
                    className="w-full h-full object-cover"
                    onError={handleImageError}
                  />
                  <div className="absolute inset-0 bg-gradient-to-r from-blue-600/20 to-purple-600/20"></div>
                </div>

                {/* Content Section */}
                <div className="p-8 lg:p-12">
                  <div className="flex flex-wrap gap-2 mb-4">
                    {caseStudies[activeCase].tags.map((tag) => (
                      <Badge key={tag} variant="secondary" className="bg-blue-100 dark:bg-blue-900 text-blue-800 dark:text-blue-200">
                        {tag}
                      </Badge>
                    ))}
                  </div>

                  <h3 className="text-2xl lg:text-3xl font-bold text-gray-900 dark:text-white mb-2">
                    {caseStudies[activeCase].title}
                  </h3>
                  
                  <p className="text-lg text-blue-600 dark:text-blue-400 mb-6">
                    {caseStudies[activeCase].industry} • {caseStudies[activeCase].client}
                  </p>

                  <div className="space-y-6">
                    <div>
                      <h4 className="font-semibold text-gray-900 dark:text-white mb-2">Challenge:</h4>
                      <p className="text-gray-700 dark:text-gray-300">{caseStudies[activeCase].challenge}</p>
                    </div>

                    <div>
                      <h4 className="font-semibold text-gray-900 dark:text-white mb-2">Solution:</h4>
                      <p className="text-gray-700 dark:text-gray-300">{caseStudies[activeCase].solution}</p>
                    </div>

                    {/* Results Grid */}
                    <div>
                      <h4 className="font-semibold text-gray-900 dark:text-white mb-4">Results:</h4>
                      <div className="grid grid-cols-2 gap-4">
                        <div className="text-center p-4 bg-green-50 dark:bg-green-900/20 rounded-lg">
                          <div className="text-2xl font-bold text-green-600 dark:text-green-400">
                            {caseStudies[activeCase].results.revenue}
                          </div>
                          <div className="text-sm text-gray-600 dark:text-gray-400">Revenue Growth</div>
                        </div>
                        <div className="text-center p-4 bg-blue-50 dark:bg-blue-900/20 rounded-lg">
                          <div className="text-2xl font-bold text-blue-600 dark:text-blue-400">
                            {caseStudies[activeCase].results.conversion}
                          </div>
                          <div className="text-sm text-gray-600 dark:text-gray-400">Conversion Rate</div>
                        </div>
                        <div className="text-center p-4 bg-purple-50 dark:bg-purple-900/20 rounded-lg">
                          <div className="text-2xl font-bold text-purple-600 dark:text-purple-400">
                            {caseStudies[activeCase].results.cac}
                          </div>
                          <div className="text-sm text-gray-600 dark:text-gray-400">CAC Reduction</div>
                        </div>
                        <div className="text-center p-4 bg-orange-50 dark:bg-orange-900/20 rounded-lg">
                          <div className="text-2xl font-bold text-orange-600 dark:text-orange-400">
                            {caseStudies[activeCase].results.timeline}
                          </div>
                          <div className="text-sm text-gray-600 dark:text-gray-400">Timeline</div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>
        </div>

        {/* Portfolio Stats */}
        <div className={`mt-16 grid md:grid-cols-4 gap-6 ${isVisible ? 'slide-up' : 'scroll-reveal'}`} style={{ animationDelay: '0.6s' }}>
          <div className="glass p-6 text-center">
            <div className="text-3xl font-bold text-blue-600 dark:text-blue-400 mb-2">500+</div>
            <div className="text-gray-700 dark:text-gray-300">Projects Completed</div>
          </div>
          <div className="glass p-6 text-center">
            <div className="text-3xl font-bold text-green-600 dark:text-green-400 mb-2">$50M+</div>
            <div className="text-gray-700 dark:text-gray-300">Revenue Generated</div>
          </div>
          <div className="glass p-6 text-center">
            <div className="text-3xl font-bold text-purple-600 dark:text-purple-400 mb-2">25+</div>
            <div className="text-gray-700 dark:text-gray-300">Industries Served</div>
          </div>
          <div className="glass p-6 text-center">
            <div className="text-3xl font-bold text-orange-600 dark:text-orange-400 mb-2">98%</div>
            <div className="text-gray-700 dark:text-gray-300">Client Satisfaction</div>
          </div>
        </div>

        {/* CTA Section */}
        <div className={`mt-16 text-center ${isVisible ? 'fade-in' : 'scroll-reveal'}`} style={{ animationDelay: '0.8s' }}>
          <div className="glass-card p-8">
            <h3 className="text-2xl font-bold text-gray-900 dark:text-white mb-4">
              Ready to Write Your Success Story?
            </h3>
            <p className="text-gray-700 dark:text-gray-300 mb-6 max-w-2xl mx-auto">
              Join hundreds of businesses that have transformed their growth trajectory with our proven strategies.
            </p>
            <button
              onClick={() => {
                const contactSection = document.getElementById('contact');
                if (contactSection) {
                  contactSection.scrollIntoView({ behavior: 'smooth' });
                }
              }}
              className="glass-button px-8 py-4 text-gray-900 dark:text-white font-semibold text-lg hover:text-gray-900 dark:hover:text-white transition-colors duration-300"
            >
              Start Your Transformation
            </button>
          </div>
        </div>
      </div>
    </section>
  );
}
