"use client";

import { useState, useEffect, useRef } from "react";

export default function AboutSection() {
  const [isVisible, setIsVisible] = useState(false);
  const sectionRef = useRef<HTMLElement>(null);

  const profileImg = "https://placehold.co/400x400?text=Professional+portrait+of+Viktor+Kominko+with+minimalistic+background";

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true);
        }
      },
      { threshold: 0.3 }
    );

    if (sectionRef.current) {
      observer.observe(sectionRef.current);
    }

    return () => observer.disconnect();
  }, []);

  const handleImageError = (e: React.SyntheticEvent<HTMLImageElement, Event>) => {
    const target = e.target as HTMLImageElement;
    target.style.display = 'none';
  };

  return (
    <section 
      id="about" 
      ref={sectionRef}
      className="py-20 px-4 bg-gradient-to-br from-slate-50 to-blue-50 dark:from-slate-900 dark:to-blue-900"
    >
      <div className="max-w-7xl mx-auto">
        <div className={`text-center mb-16 ${isVisible ? 'slide-up' : 'scroll-reveal'}`}>
          <h2 className="section-title font-bold text-gray-900 dark:text-white mb-6">
            About Viktor Kominko
          </h2>
          <p className="text-xl text-gray-600 dark:text-gray-300 max-w-3xl mx-auto">
            A visionary leader at the intersection of technology, business growth, and human potential
          </p>
        </div>

        <div className="grid lg:grid-cols-2 gap-12 items-center">
          {/* Profile Image */}
          <div className={`${isVisible ? 'scale-in' : 'scroll-reveal'}`} style={{ animationDelay: '0.2s' }}>
            <div className="glass-card p-8 text-center">
              <div className="relative inline-block">
                <img
                  src={profileImg}
                  alt="Professional portrait of Viktor Kominko with minimalistic background"
                  className="w-80 h-80 object-cover rounded-2xl mx-auto shadow-2xl"
                  onError={handleImageError}
                />
                <div className="absolute inset-0 rounded-2xl bg-gradient-to-tr from-blue-500/20 to-purple-500/20"></div>
              </div>
            </div>
          </div>

          {/* Bio Content */}
          <div className={`space-y-6 ${isVisible ? 'slide-up' : 'scroll-reveal'}`} style={{ animationDelay: '0.4s' }}>
            <div className="glass-card p-8">
              <h3 className="text-2xl font-bold text-gray-900 dark:text-white mb-4">
                Transforming Businesses Through Innovation
              </h3>
              <p className="text-gray-700 dark:text-gray-300 mb-6 leading-relaxed">
                With over a decade of experience in business transformation, Viktor Kominko has established 
                himself as a leading authority in AI-powered sales strategies, growth hacking, and digital 
                marketing innovation. His unique approach combines cutting-edge technology with deep 
                understanding of human psychology to create sustainable business growth.
              </p>
              
              <div className="grid grid-cols-2 gap-4 mb-6">
                <div className="text-center p-4 bg-white/50 dark:bg-black/20 rounded-lg">
                  <div className="text-2xl font-bold text-blue-600 dark:text-blue-400">10+</div>
                  <div className="text-sm text-gray-600 dark:text-gray-400">Years Experience</div>
                </div>
                <div className="text-center p-4 bg-white/50 dark:bg-black/20 rounded-lg">
                  <div className="text-2xl font-bold text-purple-600 dark:text-purple-400">50+</div>
                  <div className="text-sm text-gray-600 dark:text-gray-400">Industries Served</div>
                </div>
              </div>

              <p className="text-gray-700 dark:text-gray-300 leading-relaxed">
                Viktor's expertise spans across AI implementation, sales automation, marketing psychology, 
                and organizational development. He has helped Fortune 500 companies and startups alike 
                achieve unprecedented growth through data-driven strategies and innovative technology solutions.
              </p>
            </div>

            {/* Expertise Areas */}
            <div className="glass-card p-6">
              <h4 className="text-xl font-semibold text-gray-900 dark:text-white mb-4">Core Expertise</h4>
              <div className="grid grid-cols-2 gap-3">
                {[
                  "AI Sales Automation",
                  "Growth Strategy",
                  "Marketing Psychology",
                  "Data Analytics",
                  "Business Intelligence",
                  "Team Leadership",
                  "Digital Transformation",
                  "Revenue Optimization"
                ].map((skill, index) => (
                  <div 
                    key={skill}
                    className="flex items-center space-x-2 p-2 bg-white/30 dark:bg-black/20 rounded-lg smooth-transition hover:bg-white/50 dark:hover:bg-black/30"
                    style={{ animationDelay: `${0.6 + index * 0.1}s` }}
                  >
                    <div className="w-2 h-2 bg-blue-500 rounded-full"></div>
                    <span className="text-gray-700 dark:text-gray-300 text-sm font-medium">{skill}</span>
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>

        {/* Mission Statement */}
        <div className={`mt-16 text-center ${isVisible ? 'fade-in' : 'scroll-reveal'}`} style={{ animationDelay: '0.8s' }}>
          <div className="glass-card p-8 max-w-4xl mx-auto">
            <h3 className="text-2xl font-bold text-gray-900 dark:text-white mb-4">Mission</h3>
            <p className="text-lg text-gray-700 dark:text-gray-300 leading-relaxed italic">
              "To empower businesses with intelligent solutions that not only drive growth but create 
              meaningful impact in the lives of customers and communities. Through the strategic 
              implementation of AI and data-driven insights, we're building the future of business success."
            </p>
          </div>
        </div>
      </div>
    </section>
  );
}
