"use client";

import { useState, useEffect } from "react";
import { Button } from "@/components/ui/button";

export default function HeroSection() {
  const [isVisible, setIsVisible] = useState(false);

  useEffect(() => {
    setIsVisible(true);
  }, []);

  const heroBg = "https://placehold.co/1920x1080?text=Modern+minimalist+background+with+glass+effect+and+subtle+gradient";

  const handleImageError = (e: React.SyntheticEvent<HTMLImageElement, Event>) => {
    const target = e.target as HTMLImageElement;
    target.style.display = 'none';
  };

  const scrollToContact = () => {
    const contactSection = document.getElementById('contact');
    if (contactSection) {
      contactSection.scrollIntoView({ behavior: 'smooth' });
    }
  };

  return (
    <section className="relative min-h-screen flex items-center justify-center overflow-hidden">
      {/* Background Image */}
      <div className="absolute inset-0 z-0">
        <img
          src={heroBg}
          alt="Modern minimalist background with glass effect and subtle gradient"
          className="w-full h-full object-cover"
          onError={handleImageError}
        />
        <div className="absolute inset-0 bg-gradient-to-br from-blue-900/20 via-purple-900/20 to-pink-900/20"></div>
        <div className="absolute inset-0 bg-black/30"></div>
      </div>

      {/* Content */}
      <div className={`relative z-10 text-center px-4 max-w-6xl mx-auto ${isVisible ? 'fade-in' : 'opacity-0'}`}>
        <div className="glass-card p-8 md:p-12 mb-8">
          <h1 className="hero-title font-bold text-white mb-6 tracking-tight">
            Viktor Kominko
          </h1>
          <p className="text-xl md:text-2xl text-white/90 mb-4 font-light">
            Growth Strategist • AI Sales Expert • Marketing Innovator
          </p>
          <p className="text-lg md:text-xl text-white/80 mb-8 max-w-3xl mx-auto leading-relaxed">
            Transforming businesses through cutting-edge AI solutions, data-driven marketing strategies, 
            and sustainable growth frameworks that deliver measurable results.
          </p>
          
          <div className="flex flex-col sm:flex-row gap-4 justify-center items-center">
            <button
              onClick={scrollToContact}
              className="glass-button px-8 py-4 text-white font-semibold text-lg hover:text-white transition-colors duration-300"
            >
              Start Your Growth Journey
            </button>
            <button
              onClick={() => {
                const aboutSection = document.getElementById('about');
                if (aboutSection) {
                  aboutSection.scrollIntoView({ behavior: 'smooth' });
                }
              }}
              className="px-8 py-4 text-white/90 font-medium text-lg border border-white/30 rounded-xl hover:bg-white/10 transition-all duration-300"
            >
              Learn More
            </button>
          </div>
        </div>

        {/* Floating Stats */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mt-12">
          <div className="glass p-6 text-center float-animation">
            <div className="text-3xl font-bold text-white mb-2">500+</div>
            <div className="text-white/80">Businesses Transformed</div>
          </div>
          <div className="glass p-6 text-center float-animation" style={{ animationDelay: '0.5s' }}>
            <div className="text-3xl font-bold text-white mb-2">95%</div>
            <div className="text-white/80">Client Success Rate</div>
          </div>
          <div className="glass p-6 text-center float-animation" style={{ animationDelay: '1s' }}>
            <div className="text-3xl font-bold text-white mb-2">$50M+</div>
            <div className="text-white/80">Revenue Generated</div>
          </div>
        </div>
      </div>

      {/* Scroll Indicator */}
      <div className="absolute bottom-8 left-1/2 transform -translate-x-1/2 z-10">
        <div className="w-6 h-10 border-2 border-white/50 rounded-full flex justify-center">
          <div className="w-1 h-3 bg-white/70 rounded-full mt-2 animate-bounce"></div>
        </div>
      </div>
    </section>
  );
}
